<template>
    <div class="ui modal" id="editCourse">
        <i class="close icon"></i>
        <div class="header">
            Edit Course {{ course.course_name }}
        </div>
        <div class="ui form" @submit.prevent="updateCourse">
            <div class="fields">
                <div class="field">
                <label>Course Name:
                {{ course.course_name }}</label>
                </div>               
            </div>
            <div class="fields">
                <div class="field">
                <label>Start Date</label>
                    <div class="ui calendar" id="rangestart">
                        <div class="ui input left icon">
                        <i class="calendar icon"></i>
                        <input type="text" v-model="course.startDate">
                        </div>
                    </div>
                </div>
                <div class="field">
                <label>End Date</label>
                    <div class="ui calendar" id="rangeend">
                        <div class="ui input left icon">
                        <i class="calendar icon"></i>
                        <input type="text" v-model="course.endDate">
                        </div>
                    </div>
                </div>
            </div>
            <div class="fields">
                <div class="field">
                <label>Pre-Survey Link</label>
                <input type="text" v-bind:placeholder="course.presurveylink" id="pre" v-model="cpresurvey">
                </div>
                <div class="field">
                <label>Post-Survey Link</label>
                <input type="text" v-bind:placeholder="course.postsurveylink" id="post" v-model="cpostsurvey">
                </div>      
            </div>
            <div class="fields">
                <div class="field">
                    <label> Add Students to the Course </label>
                    <div class="ui left icon input">
                        <input type="text" placeholder="Add Students..." v-model="cstudents">
                        <i class="users icon"></i>
                    </div>                    
                </div>      

                <div class="field">
                    <label> Upload a CSV file instead </label>
                    <input type="file" placeholder="File Upload">
                </div>
            </div>
        </div>
        <div class="actions">
            <div class="ui black deny button">
            Cancel
            </div>
            <div class="ui positive right labeled icon button" v-on:click="createCourse">
            Done
            <i class="checkmark icon"></i>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'EditCourse',
    props: {
        course: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            cname: course.course_name,
            cstartdate: '',
            cenddate: '',
            cpresurvey: '',
            cpostsurvey: '',
            cassignstat: '',
            cstudents: ''
        }
    },
    mounted() {
        $('#rangestart').calendar({
            type: 'date',
            endCalendar: $('#rangeend'),
            onChange: function(date, text) {
              sessionStorage.setItem("start", text);
            },
            popupOptions: {
             observeChanges: false
            }
        });
        $('#rangeend').calendar({
            type: 'date',
            startCalendar: $('#rangestart'),
            onChange: function(date, text) {
              sessionStorage.setItem("end", text);
            },
            popupOptions: {
             observeChanges: false
            }            
        });
    },
    methods: {
        startdatepick: function () {
            $('#startdate_calendar').calendar({type:'date'})
        },
        enddatepick: function () {
            $('#enddate_calendar').calendar({type:'date'})
        },
        updateCourse() {
            axios.post('/courseupdate/'+this.course.id,{
                startDate: this.cstartdate,
                endDate: this.cenddate,
                presurveylink: this.cpresurvey,
                postsurveylink: this.cpostsurvey,
            })
            .then(response => {
                // redirect to user home
                this.$router.go()
            })
            .catch(error => {
                console.log(error)
                // clear form inputs
                                        $('body')
                            .toast({
                              displayTime: 5000,
                              class: 'error',
                              message: 'Cannot update course'
                            })
                          ;
            })            
        }
    }
}
</script>

<style scoped>
    .ui.form{
        padding: 20px;
    }

    .field{
        padding: 10px;
    }
</style>